import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {CountryCardModel} from './country-card-model';
import {Observable} from 'rxjs';
import {take} from 'rxjs/operators';

@Injectable()
export class CardService {
	private heroesUrl: 'https://hotchairs.herokuapp.com/countries/';

	constructor(private http: HttpClient) {}

	getHeroes(): Observable<CountryCardModel[]> {
		return this.http.get<CountryCardModel[]>(this.heroesUrl).pipe(take(1));
	}
}
