import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';

import {HttpClientModule} from '@angular/common/http';
import {AuthenticationComponent} from '@pages/authentication/authentication.component';
import {UserLoginFormComponent} from '@pages/authentication/user-login-form/user-login-form.component';
import {NotificationComponent} from '@pages/authentication/user-login-form/notification/notification.component';

import {ReactiveFormsModule} from '@angular/forms';
import {MatFormFieldModule} from '@angular/material/form-field';
import {MatInputModule} from '@angular/material/input';
import {MatButtonModule} from '@angular/material/button';
import {MatCardModule} from '@angular/material/card';
import {MatSnackBarModule} from '@angular/material/snack-bar';
import {AuthenticationService} from '@pages/authentication/authentication.service';

@NgModule({
	declarations: [AuthenticationComponent, UserLoginFormComponent, NotificationComponent],
	imports: [
		BrowserModule,
		HttpClientModule,
		ReactiveFormsModule,
		MatFormFieldModule,
		MatInputModule,
		MatButtonModule,
		MatCardModule,
		MatSnackBarModule
	],
	providers: [AuthenticationService]
})
export class AuthenticationModule {}
