import {ChangeDetectionStrategy, Component} from '@angular/core';
import {FormGroup, FormControl, Validators} from '@angular/forms';
import {Router} from '@angular/router';
import {Authentication} from '@pages/authentication/authentication';
import {AuthenticationService} from '@pages/authentication/authentication.service';
import {MatSnackBar, MatSnackBarVerticalPosition} from '@angular/material/snack-bar';
import {NotificationComponent} from '@pages/authentication/user-login-form/notification/notification.component';

@Component({
	selector: 'user-login-form',
	templateUrl: './user-login-form.component.html',
	styleUrls: ['./user-login-form.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,
	providers: [AuthenticationService]
})
export class UserLoginFormComponent {
	auth: Authentication = new Authentication();
	durationInSeconds = 5;
	verticalPosition: MatSnackBarVerticalPosition = 'top';

	constructor(
		private router: Router,
		private authenticationService: AuthenticationService,
		private _snackBar: MatSnackBar
	) {
	}

	loginForm = new FormGroup({
		username: new FormControl('', [Validators.required, Validators.pattern(/^\S*$/), Validators.email]),
		password: new FormControl('', [Validators.required, Validators.pattern(/^\S*$/)])
	});

	onLogin(auth: Authentication): any {
		if (!this.loginForm.valid) {
			this._snackBar.openFromComponent(NotificationComponent, {
				duration: this.durationInSeconds * 1000,
				verticalPosition: this.verticalPosition
			});
		} else {
			this.authenticationService.postData(auth.username, auth.password)
				.subscribe(response => {
						this.router.navigateByUrl('/main');
					} , error => {
						this._snackBar.openFromComponent(NotificationComponent, {
							duration: this.durationInSeconds * 1000,
							verticalPosition: this.verticalPosition
						});
					}
				);
		}
	}
}
