import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {Validators, ValidationErrors, ValidatorFn, AbstractControl, FormGroup, FormBuilder} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {Country, City, Office} from '@pages/office-creation/entities';
import {MatStep} from '@angular/material/stepper';
import {STEPPER_GLOBAL_OPTIONS} from '@angular/cdk/stepper';

@Component({
	selector: 'creation-form',
	templateUrl: './creation-form.component.html',
	styleUrls: ['./creation-form.component.scss'],
	providers: [
		{
			provide: STEPPER_GLOBAL_OPTIONS,
			useValue: {showError: true}
		}
	]
})
export class CreationFormComponent implements OnInit {
	filteredCountries: Observable<Country[]>;
	filteredCities: Observable<City[]>;
	formGroup: FormGroup;
	countryId: number;
	@Input() countries;
	@Input() cities;
	@Output() addOffice = new EventEmitter<any>();
	@Output() setCities = new EventEmitter<any>();
	@ViewChild('cityStep')
	cityStep: MatStep;

	constructor(private _formBuilder: FormBuilder) {}

	ngOnInit() {
		if (!this.countries) {
			this.countries = [];
		}
		if (!this.cities) {
			this.cities = [];
		}

		this.formGroup = this.buildFormGroup();

		this.filteredCountries = this.formGroup
			.get('countryFormGroup')
			.get('countryControl')
			.valueChanges.pipe(
				startWith(''),
				map((value) => this._filter(value, this.countries))
			);
	}

	private buildFormGroup(): FormGroup {
		return this._formBuilder.group({
			countryFormGroup: this._formBuilder.group({
				countryControl: ['', [this.requireCountryMatch(), Validators.required]]
			}),
			cityFormGroup: this._formBuilder.group({
				cityControl: ['', [this.requireCityMatch(), Validators.required]]
			}),
			addressFormGroup: this._formBuilder.group({
				addressControl: ['', Validators.required]
			}),
			floorFormGroup: this._formBuilder.group({
				floorControl: ['', [Validators.required, Validators.pattern('[0-9]+'), Validators.maxLength(3)]]
			})
		});
	}

	private _filter(value: any, options: any[]): any[] {
		if (!options) {
			options = [];
		}
		const filterValue = typeof value !== 'string' ? value.name.toLowerCase() : value.toLowerCase();
		return options.filter((option) => option.name.toLowerCase().includes(filterValue));
	}

	private requireMatch(control, options: any[]): boolean {
		const names = options.map((option) => {
			return option.name;
		});
		const selection: any = typeof control.value === 'string' ? control.value : control.value.name;
		if (names && !names.includes(selection)) {
			return true;
		}
		return false;
	}

	private requireCountryMatch(): ValidatorFn {
		return (control: AbstractControl): ValidationErrors | null => {
			return this.requireMatch(control, this.countries) ? {requireMatch: true} : null;
		};
	}

	private requireCityMatch(): ValidatorFn {
		return (control: AbstractControl): ValidationErrors | null => {
			return this.requireMatch(control, this.cities) ? {requireMatch: true} : null;
		};
	}

	displayValue(option): Country | City {
		return option ? option.name : option;
	}

	selectionChange(event) {
		if (event.selectedIndex === 0) {
		} else if (event.selectedIndex === 1) {
			if (this.formGroup.get('countryFormGroup.countryControl').value.id !== this.countryId) {
				this.countryId = this.formGroup.get('countryFormGroup.countryControl').value.id;

				this.setCities.emit(this.countryId);
				this.filteredCities = this.formGroup
					.get('cityFormGroup')
					.get('cityControl')
					.valueChanges.pipe(
						startWith(''),
						map((value) => this._filter(value, this.cities))
					);
			}
		}
	}

	submit(): void {
		const office: Office = {
			country: this.formGroup.get('countryFormGroup.countryControl').value.id,
			city: this.formGroup.get('cityFormGroup.cityControl').value.id,
			address: this.formGroup.get('addressFormGroup.addressControl').value.trim(),
			floor: +this.formGroup.get('floorFormGroup.floorControl').value.trim()
		};
		console.log(office);
		this.addOffice.emit(office);
	}

	onSelectionChange(event) {
		if (event.option.value.id !== this.countryId) {
			this.formGroup.get('cityFormGroup.cityControl').setErrors({incorrect: true});
			this.cityStep.completed = false;
		}
	}
}
