import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HeaderComponent} from '@shared/header/header.component';
import {AuthenticationComponent} from '@pages/authentication/authentication.component';
import {MainPageComponent} from '@pages/main-page/main-page.component';
import {OfficeCreationComponent} from '@pages/office-creation/office-creation.component';
import {DashboardComponent} from './pages/booking-search/dashboard/dashboard.component';

const routes: Routes = [
	{path: 'login', component: AuthenticationComponent},
	{path: 'main', component: HeaderComponent},
	{path: '', redirectTo: '/login', pathMatch: 'full'},
	{path: 'home', component: MainPageComponent},
	{path: 'office-creation', component: OfficeCreationComponent},
	{path: 'booking-search', component: DashboardComponent}
];

@NgModule({
	imports: [RouterModule.forRoot(routes)],
	exports: [RouterModule]
})
export class AppRoutingModule {}
