import {ChangeDetectionStrategy, Component, OnInit} from '@angular/core';
import {CountryCardModel} from '@shared/country-card/entity/country-card-model';
import {CardService} from '@shared/country-card/entity/card.service';

@Component({
	selector: 'card-grid',
	templateUrl: './card-grid.component.html',
	styleUrls: ['./card-grid.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush
})
export class CardGridComponent implements OnInit {
	countries: CountryCardModel[];

	constructor(private serviceMe: CardService) {}

	ngOnInit(): void {
		this.getSmth();
	}

	getSmth(): void {
		this.serviceMe.getHeroes().subscribe((heroes) => (this.countries = heroes));
	}
}
