import {DataSource} from '@angular/cdk/collections';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {map, take} from 'rxjs/operators';
import {Observable, of as observableOf, merge} from 'rxjs';
import {BookingSearchFiltersService} from '@pages/booking-search/booking-search-filters.service';
import {BookingTableItem} from '@pages/booking-search/booking-search-table/booking-table-item';
import {AvailableFilters} from '@pages/booking-search/dashboard/available-filters';
import {MatTableDataSource} from '@angular/material/table';

/**
 * Data source for the BookingTable view. This class should
 * encapsulate all logic for fetching and manipulating the displayed data
 * (including sorting, pagination, and filtering).
 */
export class BookingSearchTableDataSource extends DataSource<BookingTableItem> {
	data: BookingTableItem[]; /* = EXAMPLE_DATA*/
	paginator: MatPaginator;
	sort: MatSort;

	constructor(private bookingSearchFiltersService: BookingSearchFiltersService) {
		super();
	}

	/**
	 * Connect this data source to the table. The table will only update when
	 * the returned stream emits new items.
	 * @returns A stream of the items to be rendered.
	 */
	connect(): Observable<BookingTableItem[]> {
		// Combine everything that affects the rendered data into one update
		// stream for the data-table to consume.
		const dataMutations = [this.data, this.paginator.page, this.sort.sortChange];

		return merge(...dataMutations).pipe(
			map(() => {
				return this.getPagedData(this.getSortedData([...this.data]));
			})
		);
	}
	disconnect() {}
	private getPagedData(data: BookingTableItem[]) {
		const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
		return data.splice(startIndex, this.paginator.pageSize);
	}
	private getSortedData(data: BookingTableItem[]) {
		if (!this.sort.active || this.sort.direction === '') {
			return data;
		}

		return data.sort((a, b) => {
			const isAsc = this.sort.direction === 'asc';
			switch (this.sort.active) {
				case 'office':
					return compare(a.office, b.office, isAsc);
				case 'floor':
					return compare(a.floor, b.floor, isAsc);
				case 'nearestDate':
					return compare(a.nearestDate.getTime(), b.nearestDate.getTime(), isAsc);
				case 'chairId':
					return compare(a.chairId, b.chairId, isAsc);
				default:
					return 0;
			}
		});
	}
}
function compare(a: string | number, b: string | number, isAsc: boolean) {
	return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}
