import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {AvailableFilters} from '@pages/booking-search/dashboard/available-filters';
import {Subscription} from 'rxjs';
import {FormControl, FormGroup} from '@angular/forms';
import {BookingSearchFiltersService} from '@pages/booking-search/booking-search-filters.service';

@Component({
	selector: 'filterboard',
	templateUrl: './filterboard.component.html',
	styleUrls: ['./filterboard.component.scss']
})
export class FilterboardComponent implements OnInit, OnDestroy {
	@Input() selectedFilters: AvailableFilters;
	availableFilters: AvailableFilters;
	filterSubscription: Subscription;
	filterForm = new FormGroup({
		selectedOffice: new FormControl(''),
		selectedFloor: new FormControl(''),
		selectedDate: new FormControl(''),
		selectedAdditionalFilters: new FormControl(),
		selectedTimeStart: new FormControl(),
		selectedTimeEnd: new FormControl()
	});
	constructor(private bookingSearchFiltersService: BookingSearchFiltersService) {}
	getAvailableFilters(): void {
		this.filterSubscription = this.bookingSearchFiltersService
			.getAvailableFilters()
			.subscribe((availableFilters) => (this.availableFilters = availableFilters));
	}
	ngOnInit(): void {
		this.getAvailableFilters();
	}
	ngOnDestroy() {
		if (this.filterSubscription) {
			this.filterSubscription.unsubscribe();
			this.filterSubscription = null;
		}
	}
}
