import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {Country, City, Office} from '@pages/office-creation/entities';
import {ApiService} from '../../api.service';

@Injectable()
export class OfficeCreationService extends ApiService {
	getCountries(): Observable<Country[]> {
		return this.get<Country[]>('/countries');
	}

	getCities(id: number): Observable<City[]> {
		return this.get<City[]>(`/countries/cities/${id}`);
	}

	addOffice(office: Office): Observable<Office> {
		return this.post<Office>('', office);
	}
}
