import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';

@Injectable({
	providedIn: 'root'
})
export class ApiService {
	private httpOptions = {
		headers: new HttpHeaders({'Content-Type': 'application/json'})
	};
	private apiUrl = 'http://hotchairs.herokuapp.com';
	/*	private apiUrl = 'https://restcountries.eu/rest/v2/all';*/

	constructor(private http: HttpClient) {}

	get<T>(url: string): Observable<T> {
		return this.http.get<T>(this.apiUrl + url);
	}
	post<T>(url: string, data: object): Observable<T> {
		return this.http.post<T>(this.apiUrl + url, data, this.httpOptions);
	}
	put<T>(url: string, data: object): Observable<T> {
		return this.http.put<T>(this.apiUrl + url, data, this.httpOptions);
	}
	delete<T>(url: string): Observable<T> {
		return this.http.delete<T>(this.apiUrl + url, this.httpOptions);
	}
}
